import React from 'react';
import './css/App.css';

import Location from './location';
import locationList from './locationList';

function App() {

    return (
        <>
            <h1>Locations</h1>
            <div className="container">
                {
                    locationList.map((item) => {
                        return (
                            <Location key={item.id} {...item}></Location>
                        );
                    })
                }
            </div>
        </>
    );
}

export default App;
