import React from 'react';
import './css/location.css';

function Location({ id, img, where, takenBy }) {

    const showDesc = () => {
        let desc = document.getElementById('itemDesc' + id);
        desc.classList.add('show')
    }

    const hideDesc = (e) => {
        let desc = document.getElementById('itemDesc' + id);
        desc.classList.remove('show')
    }

    return (
        <>
            <div className="locationContainer" onMouseEnter={showDesc} onMouseLeave={hideDesc}>
                <img src={img} alt="" className="image" />
                <div className='locationDesc' id={'itemDesc' + id}>
                    <h3 >{where}</h3>
                    <h5 >By : {takenBy}</h5>
                </div>
            </div>
        </>
    );
}

export default Location;